import React from 'react'
import { Dispatch } from 'react';
import { UpdateVocabularyType, VocabularyType } from '../../../interfaces/interface'
import './Definition.scss'
import { useSelector } from 'react-redux';
import { RootState } from '../../../redux/store';


type Props = {
    item: VocabularyType | UpdateVocabularyType;
    onClose?: Dispatch<React.SetStateAction<boolean>>;
    check?: boolean;
    onCheck?: () => void;
}

const Definition = ({ item, onClose, check, onCheck }: Props) => {

    const { cornerButton } = useSelector((state: RootState) => state.vocabulary);
    const handlePlaySound = () => {
        const AudioPlay = new Audio(item.sound);
        AudioPlay.play();
    }
    return (
        <div className='definition'>
            <div className='definitionTop'>
                {check && <button className='definitionBtn' onClick={() => { onCheck?.() }}>
                    <i className="bx bx-check"></i>
                </button>}
                <button className='definitionBtn' onClick={handlePlaySound}>
                    <i className="bx bx-volume-low"></i>
                </button>
                {!check && <button className='definitionBtn' onClick={() => { onClose?.(false) }}>
                    <i className="bx bx-x"></i>
                </button>}
            </div>
            <div className='definitionHeader'>
                {cornerButton.active ?
                    <span className='definitionHeaderIndex'>{cornerButton.index + cornerButton.counter}.</span> : <span>Definitions of</span>
                }
                <span>{item.text}</span>
                <span>{item.class}</span>
            </div>
            <div className='definitionBody'>
                {
                    item.definitions.map((m, n) => {
                        if (item.definitions.length > 1) {
                            return <div key={n} className='sn-g' dangerouslySetInnerHTML={{ __html: `<span class="num">${n + 1}</span>` + m }} />
                        }
                        else return <div key={n} className='sn-g' dangerouslySetInnerHTML={{ __html: m }} />
                    })
                }
            </div>
        </div >
    )
}

export default React.memo(Definition)