export function sortObjectsByString(arr: any[], property: any) {
    arr.sort(function (a, b) {
        var nameA = a[property].toUpperCase(); // ignore upper and lowercase
        var nameB = b[property].toUpperCase(); // ignore upper and lowercase
        if (nameA < nameB) {
            return -1;
        }
        if (nameA > nameB) {
            return 1;
        }
        // names must be equal
        return 0;
    });
}


