import { configureStore } from "@reduxjs/toolkit";
// import counterReducer from "./counter/counterSlice";
import authSlice from "./authSlice";
import { authApi, corsApi, ggApi, mainApi, renderApi } from "../services/auth/authService";
import calendarSlice from "./calendarSlice";
import vocabularySlice from "./vocabularySlice";

export const store = configureStore({
  reducer: {
    // counter: counterReducer,
    auth: authSlice,
    calendar: calendarSlice,
    vocabulary: vocabularySlice,
    [authApi.reducerPath]: authApi.reducer,
    [mainApi.reducerPath]: mainApi.reducer,
    [corsApi.reducerPath]: corsApi.reducer,
    [ggApi.reducerPath]: ggApi.reducer,
    [renderApi.reducerPath]: renderApi.reducer,
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().concat([
      authApi.middleware,
      mainApi.middleware,
      corsApi.middleware,
      ggApi.middleware,
      renderApi.middleware,
    ])
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;