import React, { useEffect, useState } from 'react'
import './Login.scss'
import { AppDispatch, RootState } from '../../redux/store';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { userLogin, userLoginRefresh } from '../../redux/authSlice';

type Props = {}

export default function Login({ }: Props) {
  const { userLoggedIn, loading, tokenVerified } = useSelector((state: RootState) => state.auth);
  const dispatch = useDispatch<AppDispatch>();
  const [pass, setPass] = useState<string>('');
  const navigate = useNavigate();

  useEffect(() => {
    if (userLoggedIn) {
      navigate('/vocabulary')
    }
  }, [userLoggedIn])

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>): void => {
    e.preventDefault();
    tokenVerified ? dispatch(userLogin(pass)) : dispatch(userLoginRefresh(pass))
  }

  const handleChangeInput = (e: React.ChangeEvent<HTMLInputElement>): void => {
    setPass(e.currentTarget.value);
  }

  return (
    <div className='login'>
      <div className='loginContainer'>
        <form className='loginForm' onSubmit={e => handleSubmit(e)}>
          <div className='loginItem'>
            <input className={loading ? "loginInput" : "loginInput loginInputErr"} type='password' onChange={(e) => handleChangeInput(e)} autoComplete='off' />
            <button className='loginBtn' type='submit'><i className="bx bx-right-arrow-circle"></i></button>
          </div>
        </form>
      </div>
    </div>
  )
}