import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { RootState } from '../../redux/store';
import { DEFAULT_CORS_PROXY, HistoryItemType, ScheduleItemType, UpdateHistoryDataItemType, UpdateHistoryItemType, UpdateScheduleItemType, UpdateVocabularyType } from '../../interfaces/interface';



export const authApi = createApi({
    reducerPath: 'authApi',
    baseQuery: fetchBaseQuery({
        baseUrl: 'https://realm.mongodb.com/api/client/v2.0/auth/session',
        prepareHeaders: (headers, { getState }) => {
            const state = getState() as RootState;
            const token = state.auth.userToken;
            if (token) {
                headers.set('Authorization', `Bearer ${token}`);
            }
            return headers;
        },
    }),
    endpoints: (builder) => ({
        getAccessToken: builder.query({
            query: () => ({
                url: ``,
                method: 'POST',
            }),
        }),
    }),
})



export const mainApi = createApi({
    reducerPath: 'mainApi',
    baseQuery: fetchBaseQuery({
        baseUrl: 'https://ap-southeast-1.aws.data.mongodb-api.com/app/data-tcfpw/endpoint/',
        prepareHeaders: (headers, { getState }) => {
            const state = getState() as RootState;
            const token = state.auth.accessToken;
            if (token) {
                headers.set('Authorization', `Bearer ${token}`);
            }
            return headers;
        },
    }),
    endpoints: (builder) => ({
        getCollectionLength: builder.query<string, string>({
            query: (name) => `countCollection?collection=${name}`
        }),
        getVocabularyCollectionData: builder.query({
            query: (name) => `getAllData?collection=${name}`
        }),
        getScheduleCollectionData: builder.query({
            query: (name) => `getAllData?collection=${name}`,
            transformResponse: (response: ScheduleItemType[]) => {
                // Assuming response is an array of objects with _id property
                return response.sort((a, b) => +a._id - +b._id);
            },
        }),
        getHistoryCollectionData: builder.query({
            query: (name) => `getAllData?collection=${name}`,
        }),
        handleCheckItem: builder.query<any, { id: string; col: string; }>({
            query: ({ id, col }) => ({
                url: '/handleCheck',
                params: { id, col },
            })
        }),
        handleArchivedItemOnly: builder.query<any, { idd: string; col: string; pass: string }>({
            query: ({ idd, col, pass }) => ({
                url: '/searchAndArchivedOnly',
                params: { idd, col, pass },
            })
        }),
        handleArchivedItem: builder.query<any, { idd: string; ida: string; col: string; pass: string }>({
            query: ({ idd, ida, col, pass }) => ({
                url: '/searchAndArchived',
                params: { idd, ida, col, pass },
            })
        }),
        updateScheduleProgress: builder.query<any, { id: string; val: string; col: string; }>({
            query: ({ id, val, col }) => ({
                url: '/updateScheduleProgress',
                params: { id, val, col },
            })
        }),
        updateScheduleItem: builder.mutation<any, { col: string; id: string; body: UpdateScheduleItemType; }>({
            query: ({ col, id, body }) => ({
                url: '/updateScheduleItem',
                params: { col, id },
                method: 'POST',
                body: JSON.stringify(body),
            })
        }),
        updateHistoryItem: builder.mutation<any, { id: string; row: string; col: string; body: UpdateHistoryDataItemType; }>({
            query: ({ id, row, col, body }) => ({
                url: '/searchAndUpdateHistory',
                params: { id, row, col },
                method: 'POST',
                body: JSON.stringify(body),
            })
        }),
        createNewSchedule: builder.mutation<any, { col: string; body: UpdateScheduleItemType[]; }>({
            query: ({ col, body }) => ({
                url: '/createSchedule',
                params: { col },
                method: 'POST',
                body: JSON.stringify(body),
            })
        }),
        createNewHistoryItem: builder.mutation<any, { col: string; body: UpdateHistoryItemType; }>({
            query: ({ col, body }) => ({
                url: '/insertHistoryItem',
                params: { col },
                method: 'POST',
                body: JSON.stringify(body),
            })
        }),
        createNewVocabularyItem: builder.mutation<any, { col: string; body: UpdateVocabularyType; }>({
            query: ({ col, body }) => ({
                url: '/insertText',
                params: { col },
                method: 'POST',
                body: JSON.stringify(body),
            })
        }),
        updateVocabularyItem: builder.mutation<any, { id: string; col: string; body: UpdateVocabularyType; }>({
            query: ({ id, col, body }) => ({
                url: '/searchAndUpdate',
                params: { id, col },
                method: 'POST',
                body: JSON.stringify(body),
            })
        }),
        deleteVocabularyItem: builder.mutation<any, { id: string; col: string; }>({
            query: ({ id, col }) => ({
                url: '/delete',
                params: { id, col },
                method: 'GET',
            })
        }),
    }),
})

export const corsApi = createApi({
    reducerPath: 'corsApi',
    baseQuery: fetchBaseQuery({
        baseUrl: '/',
    }),
    endpoints: (builder) => ({
        getCorsLink: builder.query({
            query: (name) => ({
                url: name,
                responseHandler: (response) => response.text(),
            }),
        }),
    }),
})


export const ggApi = createApi({
    reducerPath: 'ggApi',
    baseQuery: fetchBaseQuery({
        baseUrl: 'https://script.google.com/macros/s/AKfycbwoQdwSwrevYk3Ml_61iehDX0NBqsbG5VBQqWoFZcLPPFfWMCed2rgd-JBitqXaymak/exec',
    }),
    endpoints: (builder) => ({
        getQuote: builder.query<any, { action: string; num: number; }>({
            query: ({ action, num }) => ({
                url: '',
                params: { action, num },
            })
        }),
        checkQuote: builder.query<any, { action: string; check: boolean; }>({
            query: ({ action, check }) => ({
                url: '',
                params: { action, check },
            })
        }),
    }),
})

export const renderApi = createApi({
    reducerPath: 'renderApi',
    baseQuery: fetchBaseQuery({
        baseUrl: DEFAULT_CORS_PROXY + 'https://myapp-9r5h.onrender.com',
    }),
    endpoints: (builder) => ({
        getTranslate: builder.query<any, { text: string; from: string; to: string }>({
            query: ({ text, from, to }) => ({
                url: '/trans',
                params: { text, from, to },
            })
        }),
        getWakeUp: builder.query({
            query: () => `/wakeup`
        }),


    }),
})

export const { useGetAccessTokenQuery } = authApi;
export const {
    useGetCollectionLengthQuery,
    useLazyGetCollectionLengthQuery,
    useGetVocabularyCollectionDataQuery,
    useGetHistoryCollectionDataQuery,
    useLazyGetHistoryCollectionDataQuery,
    useGetScheduleCollectionDataQuery,
    useLazyGetScheduleCollectionDataQuery,
    useHandleCheckItemQuery,
    useLazyHandleCheckItemQuery,
    useLazyHandleArchivedItemOnlyQuery,
    useLazyHandleArchivedItemQuery,
    useLazyUpdateScheduleProgressQuery,
    useUpdateScheduleItemMutation,
    useUpdateHistoryItemMutation,
    useCreateNewScheduleMutation,
    useCreateNewHistoryItemMutation,
    useCreateNewVocabularyItemMutation,
    useUpdateVocabularyItemMutation,
    useDeleteVocabularyItemMutation,
} = mainApi;

export const { useLazyGetQuoteQuery, useLazyCheckQuoteQuery } = ggApi;

export const { useGetTranslateQuery, useLazyGetTranslateQuery, useGetWakeUpQuery } = renderApi;

export const { useLazyGetCorsLinkQuery } = corsApi;
