import React, { useEffect, useState } from 'react'
import './Edit.scss'
import { DEFAULT_COLLECTION, DEFAULT_CORS_PROXY, UpdateVocabularyType, VocabularyType } from '../../../interfaces/interface';
import { useSelector } from 'react-redux';
import { RootState } from '../../../redux/store';
import Definition from '../Definition/Definition';
import { useLazyGetCorsLinkQuery, useUpdateVocabularyItemMutation } from '../../../services/auth/authService';


type Props = {
    setShowEdit: React.Dispatch<React.SetStateAction<boolean>>;
}

const Edit = ({ setShowEdit }: Props) => {
    const { vocabularyList, } = useSelector((state: RootState) => state.vocabulary);

    const [searchTerm, setSearchTerm] = useState<string>('');

    const handleKeyDown = (e: React.KeyboardEvent<HTMLDivElement>) => {
        e.stopPropagation();
    }

    const handleInputSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchTerm(e.target.value);
    };

    const handleInputSearchKeyDown = (e: React.KeyboardEvent) => {
        e.stopPropagation();
        if (e.key === ' ') {
            setSearchTerm('');
        }
        if (e.key.match(/^[1-9]$/)) {
            if (filteredResult) handleSelectSearchItem(filteredResult[Number(e.key) - 1]);
            e.preventDefault();
        }
    };

    const filterStrings = (strings: VocabularyType[], searchTerm: string) => {
        if (searchTerm.length > 2) {
            return strings.filter(str => str.text.startsWith(searchTerm));
        }
    }
    const filteredResult = filterStrings(vocabularyList, searchTerm) || [];

    const handleSelectSearchItem = (item: VocabularyType) => {
        setEditText({
            text: item.text,
            sound: item.sound,
            class: item.class,
            definitions: item.definitions,
            phonetic: item.phonetic,
            meaning: item.meaning,
            numb: item.numb,
        })
        setIdEditText(item._id);
        setSearchTerm('');
        setDefineObj(prevArray => prevArray.map(item => ({ ...item, display: true })))
    }

    let mockObj: UpdateVocabularyType = {
        text: '',
        sound: '',
        class: '',
        definitions: [],
        phonetic: '',
        meaning: '',
        numb: 0,
    }

    const [editText, setEditText] = useState<UpdateVocabularyType>(mockObj);
    const [idEditText, setIdEditText] = useState<string>('');



    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setEditText({ ...editText, [name]: value });
    }

    const handleTextAreaChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        const { value } = e.target;
        setEditText({ ...editText, definitions: JSON.parse(value) })
    }

    const [meaningText, setMeaningText] = useState({
        image: '',
        define: '',
        example: '',
        synonym: '',
        result: '',
    });

    const handleInputMeaningChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setMeaningText({ ...meaningText, [name]: value });
    }

    const handleCreateMeaningItem = () => {
        let newText = editText.text.length > 4 ? editText.text.slice(0, -2) : editText.text;
        const regText = new RegExp(`(${newText}\\w*)`, "gi");
        let res = '';
        if (meaningText.image !== '') res += `<img class=\"thumb\" src=\"${meaningText.image}\">`;
        if (meaningText.define !== '') res += `<span class=\"def\">${meaningText.define}</span>`;
        if (meaningText.example !== '') res += `<span class=\"x\">${meaningText.example.replace(regText, `<b>$1</b>`)}</span>`;
        if (meaningText.synonym !== '') res += `<span class=\"xr-gs\">synonym <small>${meaningText.synonym}</small></span>`;
        setMeaningText({
            image: '',
            define: '',
            example: '',
            synonym: '',
            result: JSON.stringify(res).slice(1).slice(0, -1),
        })
    }

    const [defineObj, setDefineObj] = useState([
        { id: 0, data: mockObj, display: false, check: false },
        { id: 1, data: mockObj, display: false, check: false },
        { id: 2, data: mockObj, display: false, check: false },
    ])

    const [getCorsLink] = useLazyGetCorsLinkQuery();

    const handleCheckDefinition = async (id: number) => {
        setDefineObj(prevArray => prevArray.map(item => item.id !== id ? { ...item, display: false, } : item));
        setEditText({ ...editText, definitions: defineObj[id].data.definitions, class: defineObj[id].data.class });
        if (editText.sound === '') {
            setEditText({ ...editText, sound: await handleFindSound() });
        }
    }


    useEffect(() => {
        if (editText.text !== '') {
            getTextDataAmerica();
            getTextDataEnglish();
            getTextDataCambridge();
        }
    }, [editText])

    const getTextDataAmerica = async () => {
        const text = editText.text;
        let result: UpdateVocabularyType = {
            text: '',
            sound: '',
            class: '',
            definitions: [],
            phonetic: '',
            meaning: '',
            numb: 210,
        }
        const baseUrl = DEFAULT_CORS_PROXY + `https://www.oxfordlearnersdictionaries.com/search/american_english/direct/?q=${text}`;
        let newText = text.length > 4 ? text.slice(0, -2) : text;
        const regText = new RegExp(`(${newText}\\w*)`, "gi");
        const pageHtml = await getCorsLink(baseUrl).unwrap();
        const parser = new DOMParser();
        const doc = parser.parseFromString(pageHtml, 'text/html');
        result.sound = doc.querySelector('.audio_play_button,.pron-us')?.getAttribute('data-src-mp3') || '';
        result.text = doc.querySelector('.webtop-g h2')?.textContent || text;
        result.class = doc.querySelector('.pos')?.textContent || '';
        const img = doc.querySelector('img.thumb')?.getAttribute('src') || '';
        let defArr: string[] = [];
        doc.querySelector('.sn-gs')?.querySelectorAll('.sn-g')?.forEach((item, index) => {
            let def = '';
            if (img && index == 0) def += `<img class="thumb" src="${img}">`;
            def += '<span class="def">' + item.querySelector('.def')?.textContent + '</span >';
            let xr = item.querySelector('.xr-gs');
            if (xr) {
                let textNodes = Array.from(xr.childNodes).map((item, index) => {
                    if (index === 0) {
                        return '<span class="xr-gs">' + item.textContent;
                    }
                    if (index === 1) {
                        return item.textContent + '<small>';
                    }
                    return item.textContent?.toLowerCase();
                }).join('');
                def += textNodes + '</small></span>';
            }
            item.querySelectorAll('.x-gs .x').forEach(k => {
                if (k.textContent) {
                    const meaningX = k.textContent.replace(regText, `<b>$1</b>`);
                    def += '<span class="x">' + meaningX + '</span>';
                }
            })
            defArr.push(def);
        });
        result.definitions = [...defArr];
        setDefineObj(prevArray => prevArray.map(item => item.id === 0 ? { ...item, data: result } : item));
    }

    const getTextDataEnglish = async () => {
        const text = editText.text;
        let result: UpdateVocabularyType = {
            text: '',
            sound: '',
            class: '',
            definitions: [],
            phonetic: '',
            meaning: '',
            numb: 210,
        }
        const baseUrl = DEFAULT_CORS_PROXY + `https://www.oxfordlearnersdictionaries.com/search/english/direct/?q=${text}`;
        let newText = text.length > 4 ? text.slice(0, -2) : text;
        const regText = new RegExp(`(${newText}\\w*)`, "gi");
        const pageHtml = await getCorsLink(baseUrl).unwrap();
        const parser = new DOMParser();
        const doc = parser.parseFromString(pageHtml, 'text/html');
        result.sound = doc.querySelector('.audio_play_button.pron-us')?.getAttribute('data-src-mp3') || '';
        result.text = doc.querySelector('.webtop h1')?.textContent || text;
        result.class = doc.querySelector('.pos')?.textContent || '';
        const img = doc.querySelector('img.thumb')?.getAttribute('src') || '';
        let defArr: string[] = [];
        doc.querySelector('ol')?.querySelectorAll('.sense')?.forEach((item, index) => {
            let def = '';
            if (img && index == 0) def += `<img class="thumb" src="${img}">`;
            def += '<span class="def">' + item.querySelector('.def')?.textContent + '</span >';
            let xr = item.querySelector('.xrefs');
            if (xr) {
                let textNodes = Array.from(xr.childNodes).map((item, index) => {
                    if (index === 0) {
                        return '<span class="xr-gs">' + item.textContent;
                    }
                    if (index === 1) {
                        return item.textContent + '<small>';
                    }
                    return item.textContent?.toLowerCase();
                }).join('');
                def += textNodes + '</small></span>';
            }
            item.querySelectorAll('span.x').forEach(k => {
                if (k.textContent) {
                    const meaningX = k.textContent.replace(regText, `<b>$1</b>`);
                    def += '<span class="x">' + meaningX + '</span>';
                }
            })
            defArr.push(def);
        });
        result.definitions = [...defArr];

        setDefineObj(prevArray => prevArray.map(item => item.id === 1 ? { ...item, data: result } : item));
    }

    const getTextDataCambridge = async () => {
        const text = editText.text;
        let result: UpdateVocabularyType = {
            text: '',
            sound: '',
            class: '',
            definitions: [],
            phonetic: '',
            meaning: '',
            numb: 210,
        }
        const baseUrl = DEFAULT_CORS_PROXY + `https://dictionary.cambridge.org/dictionary/english/${text}`;
        let newText = text.length > 4 ? text.slice(0, -2) : text;
        const regText = new RegExp(`(${newText}\\w*)`, "gi");
        const pageHtml = await getCorsLink(baseUrl).unwrap();
        const parser = new DOMParser();
        const doc = parser.parseFromString(pageHtml, 'text/html');
        result.sound = 'https://dictionary.cambridge.org' + doc.querySelector('audio#audio2 source')?.getAttribute('src') || '';
        result.text = doc.querySelector('.di-title')?.textContent || text;
        result.class = doc.querySelector('.pos.dpos')?.textContent || '';
        let defArr: string[] = [];
        doc.querySelectorAll('.def-block.ddef_block')?.forEach((item, index) => {
            let def = '';
            const img = item.querySelector('.dimg')?.querySelector('amp-img')?.getAttribute('src') || '';
            if (img) def += `<img class="thumb" src="https://dictionary.cambridge.org/${img}">`;
            def += '<span class="def">' + item.querySelector('.def')?.textContent + '</span >';
            let xs = item.querySelector('.def-body.ddef_b');
            if (xs) {
                xs.querySelectorAll('.eg.deg').forEach((m, n) => {
                    if (m.textContent) def += '<span class="x">' + m.textContent.replace(regText, `<b>$1</b>`) + "</span>";
                })
                xs.querySelectorAll('.xref').forEach((j, k) => {
                    def += '<span class="xr-gs">' + j.querySelector('.xref-title')?.textContent + ' <small>'
                    let lcs = j.querySelector('.lcs');
                    if (lcs) {
                        let textNodes = Array.from(j.querySelectorAll('.x-h.dx-h')).map((item, index) => {
                            return item.textContent?.toLowerCase();
                        }).join(', ')
                        def += textNodes + '</small></span>';
                    }
                });
            }
            defArr.push(def);
        });
        result.definitions = [...defArr];
        setDefineObj(prevArray => prevArray.map(item => item.id === 2 ? { ...item, data: result } : item));
    }

    const [updateVocabularyItem] = useUpdateVocabularyItemMutation();

    const handleUpdateText = async () => {
        try {
            updateVocabularyItem({ id: idEditText, col: DEFAULT_COLLECTION.collection, body: editText });
            setDefineObj(prevArray => prevArray.map(item => ({ ...item, display: false })))
            setEditText(mockObj);
        } catch (error) {
            console.error(error);
        }
    }

    const handleFindSound = async () => {
        const text = editText.text;
        const baseUrl = `https://www.oed.com/search/dictionary/?scope=Entries&q=${text}&tl=true`
        const pageHtml = await getCorsLink(baseUrl).unwrap();
        const parser = new DOMParser();
        const pageDoc = parser.parseFromString(pageHtml, 'text/html');
        const newUrl = 'https://www.oed.com' + pageDoc.querySelector('h3.resultTitle a')?.getAttribute('href');
        let link = newUrl.replace(/\?.+/g, "?tab=factsheet&tl=true#39853451");
        const newPageHtml = await getCorsLink(link).unwrap();
        const newPageDoc = parser.parseFromString(newPageHtml, 'text/html');
        const mp3 = newPageDoc.querySelector('.regional-pronunciation:last-child')?.querySelector('.pronunciation-play-button')?.getAttribute('data-src-mp3');
        const altMp3 = `https://ssl.gstatic.com/dictionary/static/sounds/20220808/${text}--_us_1.mp3`;
        return mp3 || altMp3;
    }



    return (
        <div className='edit' tabIndex={0} onKeyDown={handleKeyDown}>
            <div className='editTop'>
                <button className='editBtnClose' onClick={handleUpdateText}>
                    <i className="bx bx-check"></i>
                </button>
                <button className='editBtnClose' onClick={() => { setShowEdit(false) }}>
                    <i className="bx bx-x"></i>
                </button>
            </div>
            <div className='editCard'>
                <input className='editInput' value={searchTerm} onChange={handleInputSearchChange} onKeyDown={handleInputSearchKeyDown} />
                {
                    filteredResult && <div className='searchContainer'>
                        {
                            filteredResult.slice(0, 9).map((item, index) => {
                                return <div key={index} className='my-item' onClick={() => { handleSelectSearchItem(item) }}>
                                    <small>{index + 1}</small>
                                    <span>{item.text}</span>
                                </div>
                            })
                        }
                    </div>
                }
            </div>
            <div className='editCard'>
                <input className='editSerchResult' value={editText.text} name='text' onChange={handleInputChange} />

                <div className='editRowItem'>
                    <span className='editItemLabel'>PHONETIC</span>
                    <input className='editItemInput' value={editText.phonetic} name='phonetic' onChange={handleInputChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'>TYPE</span>
                    <input className='editItemInput' value={editText.class} name='class' onChange={handleInputChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'>MEANING</span>
                    <input className='editItemInput' value={editText.meaning} name='meaning' onChange={handleInputChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'>NUMB</span>
                    <input className='editItemInput' value={editText.numb} name='numb' onChange={handleInputChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'>SOUND</span>
                    <input className='editItemInput' value={editText.sound} name='sound' onChange={handleInputChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'></span>
                    <textarea className='editItemInput editItemArea' rows={2} value={JSON.stringify(editText.definitions)} name='definitions' onChange={handleTextAreaChange} />
                </div>
            </div>
            <div>
                {defineObj[0].display && <Definition item={defineObj[0].data} onCheck={() => { handleCheckDefinition(0) }} check={true} />}
                {defineObj[1].display && <Definition item={defineObj[1].data} onCheck={() => { handleCheckDefinition(1) }} check={true} />}
                {defineObj[2].display && <Definition item={defineObj[2].data} onCheck={() => { handleCheckDefinition(2) }} check={true} />}
            </div>
            <div className='editCard'>
                <div className='editRowItem'>
                    <span className='editItemLabel'>Image</span>
                    <input className='editItemInput' value={meaningText.image} name='image' onChange={handleInputMeaningChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'>Define</span>
                    <input className='editItemInput' value={meaningText.define} name='define' onChange={handleInputMeaningChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'>Example</span>
                    <input className='editItemInput' value={meaningText.example} name='example' onChange={handleInputMeaningChange} />
                </div>
                <div className='editRowItem'>
                    <span className='editItemLabel'>Synonym</span>
                    <input className='editItemInput' value={meaningText.synonym} name='synonym' onChange={handleInputMeaningChange} />
                </div>
                <div className='editRowItem'>
                    <div>
                        <button className='editBtnResult' disabled={editText.text === ''} onClick={handleCreateMeaningItem}>Result</button>
                    </div>
                    <div>{meaningText.result}</div>
                </div>
            </div>


        </div>
    )
}

export default Edit