import { authApi } from "../services/auth/authService";
import { LoginState, URL_LOGIN, URL_LOGIN_REFRESH, URL_LOGIN_UPDATE_TOKEN } from "../interfaces/interface";
import { PayloadAction, createAsyncThunk, createSlice } from "@reduxjs/toolkit";

let userLoggedIn = false;

const storageItem = sessionStorage.getItem('userInfo');
const storageUserToken = sessionStorage.getItem('userToken');
if (storageItem) {
    userLoggedIn = JSON.parse(storageItem).isVerified;
}


const initialState: LoginState = {
    loading: true,
    userLoggedIn: userLoggedIn,
    userToken: storageUserToken,
    accessToken: null,
    tokenVerified: true
};



const authSlice = createSlice({
    name: "auth",
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(userLogin.pending, (state, { payload }) => {
                state.loading = true;
            })
            .addCase(userLogin.rejected, (state, { payload }) => {
                state.loading = false;
            })
            .addCase(userLogin.fulfilled, (state, action: PayloadAction<string>) => {
                if (action.payload == "failed") {
                    state.loading = false;
                    state.userLoggedIn = false;
                }
                if (action.payload == "expired") {
                    state.tokenVerified = false;
                    state.userLoggedIn = false;
                }
                if (action.payload !== "expired" && action.payload !== "failed") {
                    state.loading = true;
                    state.tokenVerified = true;
                    state.userLoggedIn = true;
                    state.userToken = action.payload;
                }
            })
            .addCase(userLoginRefresh.pending, (state, { payload }) => {
                state.loading = true;
            })
            .addCase(userLoginRefresh.fulfilled, (state, action: PayloadAction<string | undefined>) => {
                if (action.payload == "failed") {
                    state.loading = false;
                    state.userLoggedIn = false;
                }
                else {
                    state.loading = true;
                    state.tokenVerified = true;
                    state.userLoggedIn = true;
                    state.userToken = action.payload || '';
                }
            })
            .addMatcher(authApi.endpoints.getAccessToken.matchFulfilled, (state, action) => {
                state.accessToken = action.payload.access_token;
            })
    },
});

export const userLogin = createAsyncThunk("auth/login", async (password: string) => {
    try {
        const response = await fetch(URL_LOGIN, { method: 'POST', body: JSON.stringify({ string: password }) });
        const result = await response.json();
        if (result !== "failed") {
            const decode = JSON.parse(atob(result.split('.')[1]));
            if (decode.exp * 1000 < new Date().getTime()) {
                alert("Time Expired. Please login again!");
                return "expired"
            }
            else {
                sessionStorage.setItem('userInfo', JSON.stringify({ isVerified: true }));
                sessionStorage.setItem('userToken', result);
                return result;
            }
        }
        else return result;
    } catch (error) {
        console.log(error);
    }
});


export const userLoginRefresh = createAsyncThunk("auth/loginRefresh", async (password: string) => {
    try {
        const response = await fetch(URL_LOGIN, { method: 'POST', body: JSON.stringify({ string: password }) });
        const result = await response.json();
        if (result !== "failed") {
            const response = await fetch(URL_LOGIN_REFRESH);
            const result = await response.json();
            const newResponse = await fetch(result.link, { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ token: result.value }) });
            const newResult = await newResponse.json();
            const newTokenResponse = await fetch(URL_LOGIN_UPDATE_TOKEN, { method: 'POST', body: JSON.stringify({ value: newResult.refresh_token }) });
            sessionStorage.setItem('userInfo', JSON.stringify({ isVerified: true }));
            sessionStorage.setItem('userToken', newResult.refresh_token);
            return newResult.refresh_token || "";
        }
        else return "failed";
    } catch (error) {
        console.log(error);
    }
})


export default authSlice.reducer;
