import React, { useEffect, useRef, useState } from 'react'
import './Vocabulary.scss'
import inputLeftImg from '../../assets/main/input-left-corner.png';
import inputRightImg from '../../assets/main/input-right-corner.png';
import inputBtnImg from '../../assets/main/center.png';
import audioFile from '../../assets/sound/VivaldiFourSeasonsWinter.mp3';

import Bottom from '../Bottom/Bottom'
import FlipNumber from './FlipNumber/FlipNumber'
import FlipCard from './FlipCard/FlipCard'
import { DEFAULT_COLLECTION, QuoteType, START_MINUTES, START_SECOND, TranslateType, VocabularyType } from '../../interfaces/interface';
import Definition from './Definition/Definition';
import {
  useDeleteVocabularyItemMutation,
  useGetCollectionLengthQuery,
  useGetVocabularyCollectionDataQuery,
  useLazyCheckQuoteQuery,
  useLazyGetQuoteQuery,
  useLazyGetTranslateQuery,
  useLazyHandleArchivedItemOnlyQuery,
  useLazyHandleArchivedItemQuery,
  useLazyHandleCheckItemQuery,
  useLazyUpdateScheduleProgressQuery
} from '../../services/auth/authService';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../../redux/store';
import {
  increaseCornerButtonCounter,
  resetCornerButtonCounter,
  toggleCornerButtonActive,
  toggleCornerButtonStatus
} from '../../redux/vocabularySlice';
import { updateTodaySchedule } from '../../redux/calendarSlice';
import Edit from './Edit/Edit';
import Translate from './Translate/Translate';
import { idText } from 'typescript';


type Props = {}

const Vocabulary = (props: Props) => {

  const text = {
    _id: '',
    text: '',
    sound: '',
    class: '',
    definitions: [],
    phonetic: '',
    meaning: '',
    numb: 0
  }

  const dispatch = useDispatch<AppDispatch>();
  const [currText, setCurrText] = useState<VocabularyType>(text);

  // -------------------FIND START-------------------- //

  const { refetch: refetchVocabularyList } = useGetVocabularyCollectionDataQuery(DEFAULT_COLLECTION.collection);
  const { vocabularyList, cornerButton, dailyWords } = useSelector((state: RootState) => state.vocabulary);
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [renderDefinition, setRenderDefinition] = useState<boolean>(false)
  const [handleCheckItem] = useLazyHandleCheckItemQuery();


  const handleKeyDown = (e: React.KeyboardEvent<HTMLDivElement>) => {
    if (e.key.match(/^[a-z]$/)) {
      setSearchTerm(searchTerm + e.key);
    }
    if (e.key.match(/^[1-9]$/)) {
      if (filteredResult && Number(e.key) <= filteredResult.length) handleRenderDefinition(filteredResult[Number(e.key) - 1]);
    }
    if (e.key === "Backspace") {
      setSearchTerm(searchTerm.slice(0, -1));
    }
    if (e.key === " ") {
      setSearchTerm('');
    }
  }

  const handleInputSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(e.target.value);
  };

  const handleInputSearchKeyDown = (e: React.KeyboardEvent) => {
    e.stopPropagation();
    if (e.key === ' ') {
      setSearchTerm('');
    }
    if (e.key.match(/^[1-9]$/)) {
      if (filteredResult && Number(e.key) <= filteredResult.length) handleRenderDefinition(filteredResult[Number(e.key) - 1]);
      e.preventDefault();
    }
  };

  const filterStrings = (strings: VocabularyType[], searchTerm: string) => {
    if (searchTerm.length > 2) {
      return strings.filter(str => str.text.startsWith(searchTerm));
    }
  }
  const filteredResult = filterStrings(vocabularyList, searchTerm) || [];

  const handleRenderDefinition = (item: VocabularyType) => {
    setCurrText(item);
    if (!renderDefinition) {
      setRenderDefinition(true);
    }
    setSearchTerm('');
    if (item.numb > 1) {
      handleCheckItem({ id: item._id, col: DEFAULT_COLLECTION.collection });
    }
    else handleArchivedItem(item);
  }

  const [archivedItemOnlyQuery] = useLazyHandleArchivedItemOnlyQuery();
  const [archivedItemQuery] = useLazyHandleArchivedItemQuery();
  const { refetch: refetchCollectionLength } = useGetCollectionLengthQuery(DEFAULT_COLLECTION.pass);


  const handleArchivedItem = async (item: VocabularyType) => {
    console.log(item);
    if (vocabularyList.length <= 2000) {
      await archivedItemOnlyQuery({ idd: item._id, col: DEFAULT_COLLECTION.collection, pass: DEFAULT_COLLECTION.pass });
      refetchCollectionLength();
    }
    else {
      let sliceArr = vocabularyList.slice(-(vocabularyList.length - 2000));
      const minX = sliceArr.reduce(
        (acc, curr) => (curr.numb < acc.numb ? curr : acc),
        sliceArr[0] || undefined
      );
      await archivedItemQuery({ ida: item._id, idd: minX._id, col: DEFAULT_COLLECTION.collection, pass: DEFAULT_COLLECTION.pass });
      refetchCollectionLength();
    }
  }

  // -------------------FIND END-------------------- //
  // -------------------TRANSLATE START-------------------- //
  const [translateTerm, setTranslateTerm] = useState<string>('');
  const [renderTranslate, setRenderTranslate] = useState<boolean>(false);
  const [translateData, setTranslateData] = useState<TranslateType>({
    definitions: {},
    examples: [],
    translation: '',
    translationTranscription: '',
    translations: {},
    word: '',
    wordTranscription: ''
  });
  const [getTranslate] = useLazyGetTranslateQuery();

  const handleInputTranslateChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setTranslateTerm(e.target.value);
  };

  const handleInputTranslateKeyDown = (e: React.KeyboardEvent) => {
    e.stopPropagation();
    if (e.key === ' ') {
      setTranslateTerm('');
      setRenderTranslate(false);
    }
    if (e.key === 'Enter') handleTranslate();
  };


  const handleTranslate = async () => {
    setRenderTranslate(true);
    setTranslateTerm('');
    const data = await getTranslate({ text: translateTerm, from: 'auto', to: 'vi' }).unwrap();
    setTranslateData(data);
  }
  // -------------------TRANSLATE END-------------------- //
  // -------------------QUOTE START-------------------- //

  const [quoteContent, setQuoteContent] = useState<{ isRenderQuote: boolean, item: QuoteType }>({ isRenderQuote: false, item: { check: false, value: "" } })
  const [getQuote] = useLazyGetQuoteQuery();
  const [checkQuote] = useLazyCheckQuoteQuery();

  const renderQuoteNext = async (numb: number) => {
    const data = await getQuote({ action: 'getBookmark', num: numb }).unwrap();
    setQuoteContent({ isRenderQuote: true, item: data });
  }

  const handleCheckQuote = (val: boolean) => {
    checkQuote({ action: 'setBookmark', check: val });
    setQuoteContent(prevState => ({
      ...prevState,
      item: {
        ...prevState.item,
        check: val
      }
    }));
  }

  const copyQuoteToClipboard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text);
    } catch (err) {
      console.error('Failed to copy text: ', err);
    }
  };
  // -------------------QUOTE END-------------------- //
  // -------------------TIMMER START-------------------- //
  const [timeCount, setTimeCount] = useState<number>(0)
  const [startTimer, setStartTimer] = useState<boolean>(false)
  const [audioNotification] = useState(new Audio(audioFile));

  useEffect(() => {
    if (timeCount > 0 && startTimer) {
      const intervalId = setTimeout(() => setTimeCount(timeCount - 1 * 60 * 1000), 60 * 1000);
      return () => clearInterval(intervalId);
    }
    else if (timeCount === 0 && startTimer) {
      audioNotification.play();
      showDesktopNotification();
      setStartTimer(false);
    }
  }, [timeCount]);

  const startHandler = () => {
    const timeNumb = START_SECOND * 1000 + 60 * START_MINUTES * 1000;
    if (timeCount > 0) {
      setTimeCount(0);
      setStartTimer(false);
    }
    else {
      setTimeCount(timeNumb);
      setStartTimer(true);
    }
  }

  const showDesktopNotification = () => {
    const img = "https://cdn-icons-png.flaticon.com/512/2617/2617511.png";
    const notification = new Notification("Start Focusing", {
      icon: img,
      requireInteraction: true,
    });

    notification.addEventListener("click", (e) => {
      setTimeCount(0);
      setStartTimer(false);
      audioNotification.pause();
    });

    notification.onclose = (event) => {
      setTimeCount(0);
      setStartTimer(false);
      audioNotification.pause();
      // start PlayDailyWords();
      handlePlayDailyWords();
    };
  };

  if (Notification.permission !== "granted") {
    Notification.requestPermission().then((permission) => {
      if (permission === "granted") {
        showDesktopNotification();
      }
    });
  }
  // -------------------TIMMER END-------------------- //



  // -------------------AUTO PLAY START-------------------- //
  const { todaySchedule } = useSelector((state: RootState) => state.calendar);
  const [updateScheduleProgress] = useLazyUpdateScheduleProgressQuery();
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const countRef = useRef<number>(0);

  useEffect(() => {
    if (cornerButton.active && cornerButton.counter < dailyWords.length) {
      !cornerButton.status && dispatch(toggleCornerButtonStatus());
      if (countRef.current === cornerButton.counter) startIterating();
      intervalRef.current = setInterval(() => {
        startIterating();
      }, 7500);
    } else if (!cornerButton.active && cornerButton.counter !== 0) {
      pauseIterating();
    } else if (cornerButton.active && cornerButton.counter === dailyWords.length) {
      stopIterating();
    }
    return () => {
      intervalRef.current && clearInterval(intervalRef.current);
    }
  }, [cornerButton])

  const startIterating = () => {
    // console.log('start');
    if (cornerButton.counter === 0 && (cornerButton.index === todaySchedule.startIndex1 || cornerButton.index === todaySchedule.startIndex2)) {
      // console.log('update schedule');
      const val = cornerButton.index === todaySchedule.startIndex1 ? 'time1' : 'time2';
      updateScheduleProgress({ id: todaySchedule._id, val: val, col: DEFAULT_COLLECTION.schedule });
      dispatch(updateTodaySchedule({ ...todaySchedule, [`${val}`]: todaySchedule[`${val}`] + 1 }))
    }
    handleRenderDefinition(dailyWords[cornerButton.counter]);
    dispatch(increaseCornerButtonCounter());
  }

  const pauseIterating = () => {
    console.log('pause');
    intervalRef.current && clearInterval(intervalRef.current);
    countRef.current = cornerButton.counter;
  };

  const stopIterating = () => {
    // console.log('stop');
    dispatch(resetCornerButtonCounter());
    dispatch(toggleCornerButtonActive());
    dispatch(toggleCornerButtonStatus());
    intervalRef.current && clearInterval(intervalRef.current);
    countRef.current = 0;
    //start timer
    startHandler();
  };

  const handlePlayDailyWords = () => {
    dailyWords.length > 0 && dispatch(toggleCornerButtonActive());
  }


  // -------------------AUTO PLAY END-------------------- //
  // -------------------EDIT START-------------------- //
  const [showEdit, setShowEdit] = useState<boolean>(false)
  const handleCloseEdit = () => {
    setShowEdit(!showEdit);
  }


  // -------------------EDIT END-------------------- //
  // -------------------DELETE START-------------------- //
  const [confirmDeleteId, setConfirmDeleteId] = useState(0)
  const showDeleteButton = (id: number) => {
    setConfirmDeleteId(id)
  }

  const [deleteVocabularyItem] = useDeleteVocabularyItemMutation();

  const handleDeleteVocabulary = (item: VocabularyType) => {
    try {
      deleteVocabularyItem({ id: item._id, col: DEFAULT_COLLECTION.collection });
      setSearchTerm('');
      setConfirmDeleteId(0);
    } catch (error) {
      console.error(error);
    }
  }



  // -------------------DELETE END-------------------- //


  return (
    <div className='vocabulary' tabIndex={0} onKeyDown={handleKeyDown}>
      <div className='flashCardContainer'>
        <FlipNumber item={currText} />
        <FlipCard item={currText} />
      </div>
      <div className='myInputContainer'>
        <img src={inputLeftImg} className='myInputLeftOrnament' />
        <input className='myInput' value={searchTerm} onChange={handleInputSearchChange} onKeyDown={handleInputSearchKeyDown} />
        <div className='myInputTransContent'>
          <input className='myInput' value={translateTerm} onChange={handleInputTranslateChange} onKeyDown={handleInputTranslateKeyDown} />
          <button className='myInputBtn' onClick={handleTranslate}>
            <img src={inputBtnImg} />
          </button>
        </div>
        <img src={inputRightImg} className='myInputRightOrnament' />
      </div>
      <div className='vocabularyContainer'>
        {renderTranslate && <Translate data={translateData} setRenderTranslate={setRenderTranslate} />}
        {
          filteredResult && <div className='searchContainer'>
            {
              filteredResult.slice(0, 9).map((item, index) => {
                return <div key={index} className='my-item'>
                  <span className='itemText' onClick={() => { handleRenderDefinition(item) }}>
                    <span>
                      <small>{index + 1}</small>
                      <span>{item.text}</span>
                    </span>
                    <span className='itemNumb'>{item.numb}</span>
                  </span>
                  {
                    index + 1 === confirmDeleteId && <button className='itemDeleteBtn' onClick={() => { handleDeleteVocabulary(item) }}>
                      <i className='bx bx-trash'></i>
                      {/* <i className="bx bx-x"></i> */}
                    </button>
                  }
                  {
                    index + 1 !== confirmDeleteId && <button className='itemDeleteBtn' onClick={() => { showDeleteButton(index + 1) }}></button>
                  }
                </div>
              })
            }
          </div>
        }
        {
          quoteContent.isRenderQuote && <div className='quoteContainer'>
            <div className='quoteHeader'>
              <button className='quoteBtn' onClick={() => { renderQuoteNext(-1) }}>
                <i className="bx bx-chevron-left"></i>
              </button>
              <button className={quoteContent.item.check ? 'quoteBtn quoteBtnActive' : 'quoteBtn'} onClick={() => { handleCheckQuote(!quoteContent.item.check) }}>
                {quoteContent.item.check ? <i className="bx bxs-star"></i> : <i className="bx bx-star"></i>}
              </button>
              <button className='quoteBtn' onClick={() => { renderQuoteNext(1) }}>
                <i className="bx bx-chevron-right"></i>
              </button>
              <button className='quoteBtn' onClick={() => copyQuoteToClipboard(quoteContent.item.value)}>
                <i className="bx bx-paste"></i>
              </button>
            </div>
            <div className='quoteTop'>
              <button className='quoteBtnClose' onClick={() => setQuoteContent({ ...quoteContent, isRenderQuote: false })}>
                <i className="bx bx-x"></i>
              </button>
            </div>
            <div className='quoteBody'>
              {quoteContent.item.value}
            </div>
          </div>
        }

        {renderDefinition && <Definition item={currText} onClose={setRenderDefinition} />}
      </div>

      {showEdit && <Edit setShowEdit={setShowEdit} />}
      <div className='vocabularyBtnContainer'>
        <button className='vocabularyBtn' onClick={() => renderQuoteNext(0)}>Π</button>
        <button className='vocabularyBtn' >Ν</button>
        <button className='vocabularyBtn' >Ε</button>
        <button className='vocabularyBtn' onClick={handleCloseEdit} >Λ</button>
        <button className='vocabularyBtn' onClick={refetchVocabularyList} >Δ</button>
        <button className='vocabularyBtn' onClick={startHandler}>Χ</button>
        <button className='vocabularyBtn'>Σ</button>
        <button className='vocabularyBtn' >Ξ</button>
        <button className={timeCount > 0 ? 'vocabularyBtn timerBtn timerBtnActive' : 'vocabularyBtn timerBtn'} onClick={startHandler}>{timeCount / 60000}m</button>
      </div>
      <Bottom playDailyWords={handlePlayDailyWords} />
    </div>
  )
}

export default Vocabulary