import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { CalendarState, ScheduleItemType, formatDate } from "../interfaces/interface";
import { mainApi } from "../services/auth/authService";


let today = {
    _id: '',
    date: '',
    startIndex1: 0,
    startIndex2: 0,
    time1: 0,
    time2: 0,
}

const initialState: CalendarState = {
    schedule: [],
    todaySchedule: today,
    history: [],
};



const calendarSlide = createSlice({
    name: "calendar",
    initialState,
    reducers: {
        updateTodaySchedule: (state, action) => {
            state.todaySchedule = action.payload;
        },
    },
    extraReducers: (builder) => {
        builder
            .addMatcher(mainApi.endpoints.getScheduleCollectionData.matchFulfilled, (state, action: PayloadAction<ScheduleItemType[]>) => {
                const date = new Date();
                const today = action.payload.find((item) => item.date === formatDate(date));
                if (today) state.todaySchedule = today;
                state.schedule = [...action.payload].sort((a, b) => a['_id'].localeCompare(b['_id']));
            })
            .addMatcher(mainApi.endpoints.getHistoryCollectionData.matchFulfilled, (state, action) => {
                state.history = [...action.payload].sort((a, b) => a['index'] - b['index']);
            })
    },
});

export const {
    updateTodaySchedule
} = calendarSlide.actions;

export default calendarSlide.reducer;
