import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { CornerButtonType, VocabularyState } from "../interfaces/interface";
import { mainApi } from "../services/auth/authService";

let button = {
    active: false,
    index: 0,
    counter: 0,
    status: false,
}

const initialState: VocabularyState = {
    vocabularyList: [],
    dailyWords: [],
    cornerButton: button,
};



const vocabularySlide = createSlice({
    name: "vocabulary",
    initialState,
    reducers: {
        setDailyWords: (state, action) => {
            const num = action.payload % 50;
            state.dailyWords = state.vocabularyList.slice(action.payload, action.payload + 50 - num);
            state.cornerButton.index = action.payload;
            state.cornerButton.counter = 0;
        },
        increaseCornerButtonCounter: (state) => {
            state.cornerButton.counter = state.cornerButton.counter + 1;
        },
        resetCornerButtonCounter: (state) => {
            state.cornerButton.counter = 0;
        },
        setCornerButtonIndex: (state, action: PayloadAction<number>) => {
            state.cornerButton.index = action.payload;
        },
        toggleCornerButtonActive: (state) => {
            state.cornerButton.active = !state.cornerButton.active;
        },
        toggleCornerButtonStatus: (state) => {
            state.cornerButton.status = !state.cornerButton.status;
        },


    },
    extraReducers: (builder) => {
        builder
            .addMatcher(mainApi.endpoints.getVocabularyCollectionData.matchFulfilled, (state, action) => {
                state.vocabularyList = [...action.payload].sort((a, b) => a['_id'].localeCompare(b['_id']));
            })
            .addMatcher(mainApi.endpoints.handleCheckItem.matchPending, (state, action) => {
                const { id } = action.meta.arg.originalArgs;
                const text = state.vocabularyList.find((item) => item._id === id);
                if (text) {
                    text.numb = text.numb - 1;
                }
            })
            .addMatcher(mainApi.endpoints.handleArchivedItem.matchFulfilled, (state, action) => {
                const { idd, ida } = action.meta.arg.originalArgs;
                let textUpdate = state.vocabularyList.find((item) => item._id === ida);
                let textDelete = state.vocabularyList.find((item) => item._id === idd);
                if (textUpdate) {
                    textUpdate = { ...textUpdate, ...textDelete, _id: textUpdate._id };
                }
                state.vocabularyList = [...state.vocabularyList].filter(item => item._id !== idd);
            })
            .addMatcher(mainApi.endpoints.handleArchivedItemOnly.matchFulfilled, (state, action) => {
                const { idd } = action.meta.arg.originalArgs;
                state.vocabularyList = [...state.vocabularyList].filter(item => item._id !== idd);
            })
            .addMatcher(mainApi.endpoints.updateVocabularyItem.matchFulfilled, (state, action) => {
                const { id, body } = action.meta.arg.originalArgs;
                state.vocabularyList = [...state.vocabularyList].map(item => item._id === id ? { ...item, ...body } : item);
            })
            .addMatcher(mainApi.endpoints.deleteVocabularyItem.matchFulfilled, (state, action) => {
                const { id } = action.meta.arg.originalArgs;
                state.vocabularyList = [...state.vocabularyList].filter(item => item._id !== id);
            })
    },
});

export const {
    setDailyWords,
    setCornerButtonIndex,
    increaseCornerButtonCounter,
    resetCornerButtonCounter,
    toggleCornerButtonActive,
    toggleCornerButtonStatus,
} = vocabularySlide.actions;

export default vocabularySlide.reducer;
