import './App.scss';
import './common.scss';
import { HashRouter, Navigate, Route, Routes } from 'react-router-dom';
import Login from './pages/Login/Login';
import Vocabulary from './pages/Vocabulary/Vocabulary';
import Calendar from './pages/Calendar/Calendar';
import Weather from './pages/Weather/Weather';
import { useSelector } from 'react-redux';
import { RootState } from './redux/store';
import { Suspense, useEffect, useState } from 'react';
import React from 'react';
const Layout = React.lazy(() => import('./pages/Layout/Layout'));



function App() {

  const { userLoggedIn } = useSelector((state: RootState) => state.auth);

  const PrivateRoutes = ({ isAuth }: { isAuth: boolean }) => {
    return isAuth ? <Suspense fallback={<div>Loading...</div>}><Layout /></Suspense> : <Navigate to='/' />
  }



  return (
    <>
      <HashRouter>
        <Routes>
          <Route index path='/' element={<Login />} />
          <Route element={<PrivateRoutes isAuth={userLoggedIn} />}>
            <Route path='/vocabulary' element={<Vocabulary />} />
            <Route path='/calendar' element={<Calendar />} />
            <Route path='/weather' element={<Weather />} />
            <Route path="*" element={<p>There's nothing here: 404!</p>} />
          </Route>
        </Routes>
      </HashRouter >
    </>
  );
}

export default App;
