import React, { useEffect } from 'react'
import cup from "../../../assets/main/cup.png";
import './FlipNumber.scss'
import { VocabularyType } from '../../../interfaces/interface';

type Props = {
    item: VocabularyType
}

const FlipNumber = ({ item }: Props) => {

    useEffect(() => {
        if (item.text !== '') {
            if (item.numb > 1) {
                renderIndicateFlipContent(item.numb);
                let timer = setTimeout(() => {
                    renderIndicateFlipContent(item.numb - 1);
                }, 2500);
                return () => {
                    clearTimeout(timer);
                };
            }
            else {
                const numLists = document.getElementsByClassName('imgList');
                numLists[0].setAttribute("style", `transform: translateY(0px)`);
                let timer = setTimeout(() => {
                    numLists[0].setAttribute("style", `transform: translateY(45px)`);
                }, 2500);
                return () => {
                    clearTimeout(timer);
                };
            }
        }
    }, [item.numb])


    const renderIndicateFlipContent = (num: number) => {
        let numLists = Array.from(document.getElementsByClassName('numList'));
        let numStr = "" + num;
        if (numStr.length < 2) numStr = "00" + numStr;
        if (numStr.length < 3) numStr = "0" + numStr;
        numLists.forEach((ele, i) => {
            let n = +numStr[i];
            let offset = n * 18;
            ele.removeAttribute("hidden");
            if (i === 0 && num < 100) ele.setAttribute("hidden", "true");
            if (i === 1 && num < 10) ele.setAttribute("hidden", "true");
            ele.setAttribute("style", `transform: translateY(-${offset}px)`);
        })
    }

    return (
        <div className='flipCardLeftContent'>
            <div className='numberFlipContainer'>
                <div className='numberFlipContent'>
                    <div className='flipNumber'>
                        {
                            item.numb > 1 ?
                                <div className="numListContent">
                                    <div className="numList">
                                        <div className="number">0</div>
                                        <div className="number">1</div>
                                        <div className="number">2</div>
                                    </div>
                                    <div className="numList">
                                        <div className="number">0</div>
                                        <div className="number">1</div>
                                        <div className="number">2</div>
                                        <div className="number">3</div>
                                        <div className="number">4</div>
                                        <div className="number">5</div>
                                        <div className="number">6</div>
                                        <div className="number">7</div>
                                        <div className="number">8</div>
                                        <div className="number">9</div>
                                    </div>
                                    <div className="numList">
                                        <div className="number">0</div>
                                        <div className="number">1</div>
                                        <div className="number">2</div>
                                        <div className="number">3</div>
                                        <div className="number">4</div>
                                        <div className="number">5</div>
                                        <div className="number">6</div>
                                        <div className="number">7</div>
                                        <div className="number">8</div>
                                        <div className="number">9</div>
                                    </div>
                                </div>
                                : <div className='imgFlipContent'>
                                    <div className="imgList">
                                        <img className='imgFlip' src={cup} />
                                        <span className="imgNumber">1</span>
                                    </div>
                                </div>
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default React.memo(FlipNumber)