import React, { useRef, useState } from 'react'
import './Bottom.scss'
import { NavLink } from 'react-router-dom'
import { useGetAccessTokenQuery, useGetCollectionLengthQuery, useGetWakeUpQuery } from '../../services/auth/authService'
import { DEFAULT_COLLECTION } from '../../interfaces/interface'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../redux/store'
import { setDailyWords } from '../../redux/vocabularySlice'

type Props = {
    playDailyWords?: () => void
}

const Bottom = ({ playDailyWords }: Props) => {
    //refresh token after 1620000 ms if user already login
    const { data: dataToken } = useGetAccessTokenQuery(undefined, {
        pollingInterval: 1620000,
    });
    //Wakeup sever render after 14 minutes
    const { isSuccess: isWakeupSuccess } = useGetWakeUpQuery({
        pollingInterval: 840000,
    });

    const { data: colLength } = useGetCollectionLengthQuery(DEFAULT_COLLECTION.pass);

    const { cornerButton, dailyWords } = useSelector((state: RootState) => state.vocabulary);
    const [inputWordRow, setInputWordRow] = useState<number>(cornerButton.index)
    const dispatch = useDispatch();

    const handleChangeInputWordRow = (e: React.ChangeEvent<HTMLInputElement>) => {
        setInputWordRow(Number(e.target.value));
    }

    const handleKeydownInputWordRow = (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === "Enter") {
            dispatch(setDailyWords(inputWordRow - 1));
            e.currentTarget.blur();
        }
    }

    return (
        <div className='bottom'>
            <NavLink to="/vocabulary" className="mainFooterBtn">Übermensch</NavLink>
            <NavLink to="/calendar" className="mainFooterBtn">Amor fati</NavLink>
            <div className={isWakeupSuccess ? 'dotNum dotNumActive' : 'dotNum'}>{colLength ? colLength : 229}</div>
            <NavLink to="/weather" className="mainFooterBtn">Caelus</NavLink>
            <div className={cornerButton.active ? 'mainFooterCornerBtn mainFooterCornerBtnActive' : 'mainFooterCornerBtn'} onClick={playDailyWords}>
                <input className='inputWordRow' value={inputWordRow} onClick={(e) => e.stopPropagation()} onChange={handleChangeInputWordRow} onKeyDown={handleKeydownInputWordRow} />
                <div className={cornerButton.status ? 'cornerDot cornerDotActive' : 'cornerDot'}></div>
                <div className={dailyWords.length > 0 ? 'cornerDaily cornerDailyActive' : 'cornerDaily'}>...</div>
            </div>
        </div>
    )
}

export default Bottom